<template>
<div class="my_skills">
    <p class="a c" style="font-family: Karla; font-size: 35px; margin-top: 40px;">My Badges<span style="margin-left: 10px; font-size: 20px;">(from <a href="https://github.com/rodukov" class="link">GitHub</a>)</span></p>
    <div v-html="data" class="data" style="backdrop-filter: blur(100px); ">
    </div>
</div>
</template>

<script>
import axios from 'axios'

export default {
    name: 'my_skills',
    data() {
        return {
            data: ""
        } 
    },
    methods: {
        async request() {
        await axios
            .get('https://raw.githubusercontent.com/rodukov/rodukov/main/README.md')
            .then(response => {
                this.data = response["data"]
                this.data = this.data.replace('### Hi! Good to see you on my Github page! ', '').replace('I publish open source software here. All the repositories are at your disposal.', '')
            })
            .catch(error => console.log(error) )
            console.log(this.error_axios)
        },
    },
    mounted: function() {
        this.request()
    }
}
</script>

